<template>
	<view>
		<pageIndex
		:isIn="pageComponent=='pageIndex'"
		 v-show="pageComponent == 'pageIndex'"></pageIndex>
		<pageCommunity
		@toIndex="toIndex"
		 v-show="pageComponent == 'pageCommunity'"></pageCommunity>
		<pageMy v-show="pageComponent == 'pageMy'"></pageMy>
		<pageMessage
		@toIndex="toIndex"
		 v-show="pageComponent == 'pageMessage'"></pageMessage>

		<tabbar :pageComponent="pageComponent" @tabbarClick="tabbarClick"></tabbar>
		<gift-popup></gift-popup>

		<u-toast ref="uToast"></u-toast>

	</view>
</template>

<script>
import tabbar from './tabbar/tabbar.vue';
import pageIndex from './switch/pageIndex.vue';
import pageCommunity from './switch/pageCommunity.vue';
import pageMy from './switch/pageMy.vue';
import pageMessage from './switch/pageMessage.vue';

export default {
	components: {
		tabbar,
		pageIndex,
		pageCommunity,
		pageMy,
		pageMessage
	},
	data() {
		return {
			pageComponent: 'pageIndex',
		};
	},
	mounted() {
		
		
		if (uni.getStorageSync('pageGetLocationFail') != 1) {
			uni.getLocation({
				type: 'gcj02',
				success: (res) => {
					var latitude = res.latitude.toFixed(5);
					var longitude = res.longitude.toFixed(5);

					this.$kt.request.request({
						api: this.$api.business.ovo.userBind.updateLocation,
						data: {
							latitude,
							longitude
						},
						stateSuccess: (res1) => {
							uni.setStorageSync('pageGetLocationFail', "0");
							this.$ovo.setSelf(res1.data);
						}
					})
				},
				fail: (err) => {
					console.log(err);
					if (err.errMsg == "getLocation:fail auth deny") {
						uni.setStorageSync('pageGetLocationFail', "1");
					}
				}
			});
		}
	},
	methods: {
		toIndex() {
			this.pageComponent = 'pageIndex';
		},
		tabbarClick(pageComponent) {
			this.pageComponent = pageComponent;
		}
	},
}
</script>

